
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;




















@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.inner-message {
  position: relative;
  height: 100%;
  font-size: 12px;

  .ant-table {
    font-size: 12px;
  }

  .unread-dot {
    display: block;
    color: #f50;
    transform: scale(.9);
  }

  .unread-row {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }

  .action-group {
    position: absolute;
    bottom: 18px;
    left: 20px;

    .ant-btn {
      margin-right: 15px;
    }
  }
}